.main-info {
  margin-bottom: $margin-section  ;
}

.main-info-container {
  display: flex;
  justify-content: space-between;
}

.main-info-content {
  width: 100%;
  max-width: 904px;

  &__title {
    margin-bottom: $margin-16;
    font: $font-formular-title;
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__places {
    margin-right: $margin-16;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
  }

}

.block {
  padding: 24px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 7px 13px rgba(49, 71, 129, 0.07);

}

.main-info-places {
  display: flex;
  margin-bottom: $margin-16;
  width: 100%;
  max-width: 444px;

  :first-child {
    margin-right: $margin-16;
  }

  &__block {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 190px;
  }

  &__count {
      font: $font-formular-title;
      color: $color-action;
      transition: color 0.2s ease-in-out;

      &:hover {
        color: #222;
        transition: color 0.2s ease-in-out;
      }
  }

  &__count--rating {
    color: #222;
    transition: color 0.2s ease-in-out;

    &:hover {
      color: $color-action;
      transition: color 0.2s ease-in-out;
    }
  }

  &__descr {
    color: $color-dscr;
  }
}

.main-info-advantages {
  margin-right: $margin-16;
  margin-bottom: $margin-16;
  padding: 17px 24px;
  width: 100%;
  max-width: 660px;

  &__stroke {
    padding: 8px 0;
    display: inline-flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;

  }
  &__stroke:not(:last-child) {
    border-bottom: 1px solid $color-border;
  }

  &__descr {
    font: $font-opensans-descr;
    color: $color-dscr;
  }  
}

.main-info-login {
  display: flex;
  flex-direction: column;
  margin-bottom: $margin-16;

  &__question {
    margin-bottom: $margin-16;
    padding-top: 23px;
    font: $font-opensans-liltitle;
  }
}

.login-link {
  padding: 17px 22px;
  font-family: $Formular;
  font-size: 10px;
  font-weight: 500;
  line-height: $margin-16;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 5px;
  border: 1px solid $color-dscr;
}

.main-info-pass {

  &__title {
    margin-bottom: $margin-16;
    font: $font-formular-liltitle;
  }
}

.block-chart {
  max-width: 100%;
  max-height: 307px;
}


@media (max-width:991px) {

  .main-content {
    max-width: 100%;
  }

  .main-info-content {
    max-width: 100%;

     &__title {
       text-align: center;
     }

     &__top {
       flex-direction: column;
     }

     &__places {
       margin-right: 0;
     }
  }

  .main-info-places {
    max-width: 100%;
  }
}

@media (max-width:500px) {

  .main-info {
    margin-bottom: 40px;
    &__advantages {
      margin-right: 0;
    }
  }

  .main-info-content {
    &__title {
      font: $font-formular-mobile-title;
    }

    &__bottom {
      flex-direction: column;
    }
  }

  .main-info-places {
    &__count {
      font-size: 23px;
      line-height: 26px;
    }
  }

  .block {
    padding: $margin-16;
  }

  .main-info-login {
    &__question {
      padding-top: 0;
    }
  }

  .main-info-pass {
    &__title {
      text-align: center;
      line-height: 22px;
    }
  }
}