.about {
  position: relative;

  &__title {
    margin-bottom: $margin-16;
    font: $font-formular-title;
  }

  &__pictures {
    display: grid;
    margin-bottom: $margin-little;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(2, 176px);
    gap: 8px;
  }

  &__description {
    font: $font-opensans-descr;
  }
}

.back-title-about {
  position: absolute;
  top: 0;
  right: 0;
  font: $font-opensans-link;
  color: $color-dscr;
}

.about-img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  &--1 {
    border-radius: 10px 0 0 10px;
  }

  &--3 {
    border-top-right-radius: 10px;
  }

  &--5 {
    border-bottom-right-radius: 10px;
  }
}

.about-pic {
  position: relative;
  display: block;

  &--1 {
    grid-column: 1/7;
    grid-row: 1/3;
  }

  &--2 {
    grid-column: 7/9;
    grid-row: 1;
  }
  
  &--3 {
    grid-column: 9/11;
    grid-row: 1;
  }

  &--4 {
    grid-column: 7/9;
    grid-row: 2;
  }

  &--5 {
    grid-column: 9/11;
    grid-row: 2;
  }
}

.btn-about-showmore {
  padding: 0;
  font: $font-formular-liltitle;
  color: $color-action;
}

@media (max-width:500px) {
  .about {

    &__description {
      padding: 24px;
    }

  }
}