.container {
  max-width: 1366px;
  padding: 0 75px;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.content {
  display: flex;
  justify-content: space-between;
}

.main-content {
  width: 100%;
  max-width: 904px
}

@media (max-width:1200px) {
  .container {
    max-width: 1200px;
    padding: 0 60px;
  }
}

@media (max-width:991px) {
  .container {
    max-width: 991px;
    padding: 0 30px;
  }
}

@media (max-width:500px) {
  .container {
    max-width: 500px;
    padding: 0 15px;
  }
}

//маленькие компоненты
@import "./components/sidebar";


// большие компоненты
@import "./components/header";
@import "./components/header-page";
@import "./components/main-info";
@import "./components/specialties";
@import "./components/about";