.sidebar {
  margin-right: 32px;
  width: 100%;
  max-width: 280px;
}

.nav {
  padding: 10px 14px 0 18px;
  padding-right: 14px;
  padding-left: 18px;

  &__list {
    width: 100%;
  }

  &__item {
    position: relative;

    ::before {
      content: '';
        position: absolute;
        top: 45%;
        right: 0;
        bottom: 0;
        left: -10px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $color-action;
        opacity: 0;
        transition: opacity 0.2s ease-in-out;
    }

    &:hover {
      ::before {
        opacity: 1;
        transition: opacity 0.2s ease-in-out;
      }
    }
    &:active {
      ::before {
        opacity: 0;
        transition: opacity 0.2s ease-in-out;
      }
    }
  }

  &__item:not(:last-child) {
    margin-bottom: $margin-16;
  }

  &__item:last-child {
    ::before {
      top: 20%;
    }
  }

  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Open Sans';
    font-size: 15px;
    line-height: 20px;
    letter-spacing: 0.5px;
    color: $color-dscr;
    transition: color 0.2s ease-in-out;

    &:hover {
      color: $color-action;
      transition: color 0.2s ease-in-out;
    }

    &:active {
      color: #222;
      transition: color 0.2s ease-in-out;
    }

  }

  &__count {
    padding: 8px;
    font-family: 'Formular';
    font-size: 12px;
    line-height: $margin-16;
    letter-spacing: 2px;
    background-color: #fff;
    border-radius: 10px;
  }
}

@media (max-width:1200px) {
  
  .sidebar {
    margin-right: 0;
  }

}

@media (max-width:991px) {

  .sidebar {
    display: none;
  }
  
}