}
a:hover {
  color: #0656b4
}
.edu-confirm {
  color: #abc8ea
}
.edu-confirm, .edu-warning {
  font-size: 14px;
  margin-left: 5px
}
.edu-warning {
  color: red
}
button:focus, input:focus {
  outline: none!important
}
input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none
}
.s-btn {
  font-family: Formular;
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: all .3s;
  border-radius: 5px;
  padding: 16px 18px
}
body.popup {
  overflow: hidden
}
.bg-gray {
  background: #fbfbfb!important
}
@media (min-width:992px) {
  .s-btn {
    font-size: 12px;
    line-height: 16px;
    padding: 15px 42px
