  margin-bottom: 16px
}
.quiz-radios label {
  display: flex;
  font-size: 13px;
  line-height: 20px;
  border: 1px solid #d8d8d8;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
  align-items: center;
  transition: all .3s;
  height: 100%
}
.quiz-form button[disabled=disabled] {
  cursor: not-allowed;
  opacity: 1;
  background-color: #a7a7a7;
  pointer-events: all
}
.quiz-radios label.active {
  border-color: #0656b4
}
.quiz-radios label input[type=radio] {
  display: none
}
.quiz-radios label span {
  border: 1px solid #d8d8d8;
  border-radius: 3px;
  margin-right: 8px;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  transition: all .3s
}
.quiz-radios label em {
  font-size: 12px;
  font-style: normal;
  color: #777
}
.quiz-radios label input[type=radio]:checked+span {
  background-color: #0656b4;
  border-color: #0656b4;
  position: relative
}
.quiz-radios label input[type=radio]:checked+span:after {
  content: "";
  background: #fff;
  width: 10px;
  height: 2px;
  position: absolute;
  transform: rotate(-45deg);
  top: 6px;
  right: 1px
}
.quiz-radios label input[type=radio]:checked+span:before {
  content: "";
  background: #fff;
  width: 5px;
  height: 2px;
  position: absolute;
  transform: rotate(45deg);
  top: 8px;
  left: 1px
}
