.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.btn-text {
  font-family: Formular;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 2px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}

.m-formular-11 {
  display: flex;
  align-items: center;
  text-align: center;
  color: #222;
}

.m-formular-11, .tab-text {
  font-family: Formular;
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
  line-height: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.h1, .h2, .h3, .h4, .h5, h1, h2, h3, h4, h5 {
  font-family: Formular;
  font-style: normal;
  margin: 0;
}

.h1, h1 {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
}

.h2, .h3, h2, h3 {
  font-weight: 500;
  font-size: 23px;
  line-height: 26px;
}

.h4, h4 {
  font-size: 18px;
  line-height: 22px;
}

.h4, .h5, h4, h5 {
  font-weight: 500;
}

.h5, h5 {
  font-size: 15px;
  line-height: 20px;
}

.text-read {
  font-size: 15px;
  line-height: 25px;
}

.descriptor, .text-read {
  font-style: normal;
  font-weight: 400;
}

.descriptor {
  font-size: 12px;
  line-height: 20px;
  color: #767676;
}

.link {
  font-weight: 600;
  font-size: 13px;
  line-height: 20px;
}

.link:hover {
  cursor: pointer;
}

.link-more, .link:hover {
  color: #0656b4;
}

.link-all {
  font-size: 16px;
}

.category, .link-all {
  font-weight: 600;
  line-height: 20px;
}

.category {
  font-size: 9px;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-feature-settings: "tnum" on, "lnum" on;
  color: #767676;
}

.light-blue-btn {
  font-family: Formular;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #0656b4;
  background-color: #dfe9f5;
  border-radius: 5px;
  transition: all 0.3s;
  height: 48px;
  display: inline-block;
  line-height: 48px;
  text-align: center;
  padding: 0 20px;
}

.light-blue-btn:hover {
  background-color: rgba(223, 233, 245, 0.5);
}

.light-blue-btn:active {
  transform: translateY(-2px);
}

.signature {
  font-size: 13px;
  line-height: 20px;
}

.c-gray {
  color: #767676;
}

.no-h {
  font-family: Open Sans;
  margin: 0;
}

@media (min-width: 992px) {
  .h1, h1 {
    font-size: 50px;
    line-height: 60px;
  }

  .h2, h2 {
    font-weight: 700;
    font-size: 40px;
    line-height: 45px;
  }

  .h3, h3 {
    font-size: 31px;
    line-height: 36px;
  }

  .h3, .h4, h3, h4 {
    font-weight: 500;
  }

  .h4, h4 {
    font-size: 18px;
    line-height: 24px;
