.specialties-payment-block__title {
  margin-bottom: 8px;
  font: 500 18px/24px "Formular";
}
.specialties-payment-block__list {
  display: flex;
  flex-wrap: wrap;
}
.specialties-payment-block__item {
  margin-bottom: 8px;
}
.specialties-payment-block__item:not(:last-child) {
  margin-right: 6px;
}
.specialties-payment-block__link {
  display: inline-block;
  padding: 5px 12px;
  font-family: "Open Sans";
  font-size: 14px;
  line-height: 22px;
  white-space: nowrap;
  border: 1px solid #D8D8D8;
  border-radius: 47px;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}
.specialties-payment-block__link:hover {
  border: 1px solid #0656B4;
  background-color: #0656B4;
  color: #fff;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}
.specialties-payment-block__descr {
  margin-bottom: 0;
  font: 400 13px/20px "Open Sans";
  line-height: 25px;
  color: #767676;
}
.specialties-payment-block__openall {
  font-family: "Open Sans";
  font-size: 13px;
  font-weight: 600;
  line-height: 25px;
}

.specialties-payment-block-right {
  padding-left: 16px;
  width: 100%;
  max-width: 270px;
}
.specialties-payment-block-right__list {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.specialties-payment-block-right__item {
  display: flex;
  padding: 12px 0;
}
.specialties-payment-block-right__item:first-child {
  padding-top: 0;
}
.specialties-payment-block-right__item:not(:last-child) {
  border-bottom: 1px solid #D8D8D8;
}
.specialties-payment-block-right__item {
  display: flex;
  justify-content: space-between;
}
.specialties-payment-block-right__about {
  font: 400 12px/20px "Open Sans";
  color: #767676;
}
.specialties-payment-block-right__count {
  font-family: "Formular";
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}

.specialties-payment-block-right-icon {
  display: flex;
}
.specialties-payment-block-right-icon__item:not(:last-child) {
  margin-right: 9px;
}

.btn-show-all-specialties {
  padding: 15px 22px;
  font-family: "Formular";
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: rgba(6, 86, 180, 0.13);
  color: #0656B4;
}

.specialties-payment-block.is-hidden {
  display: none;
}

@media (max-width: 1200px) {
  .specialties-filter {
    flex-direction: column;
  }
  .specialties-filter__title {
    align-self: flex-start;
  }
  .specialties-filter__list {
    width: 100%;
  }
  .specialties-filter__btn {
    text-align: left;
  }

  .specialties-payment__block {
    flex-direction: column;
  }

  .specialties-payment-block__left {
    flex-direction: column;
  }
  .specialties-payment-block__pic {
    margin-right: 0;
    margin-bottom: 16px;
  }
  .specialties-payment-block__content {
    padding-right: 0;
    max-width: 100%;
    border: none;
  }
  .specialties-payment-block__qualification {
    display: block;
    margin-bottom: 10px;
  }
  .specialties-payment-block__descr {
    margin-bottom: 10px;
  }
  .specialties-payment-block__openall {
    display: none;
  }
  .specialties-payment-block__right {
    padding-left: 0;
    max-width: 100%;
  }
}
@media (max-width: 1200px) {
  .specialties__title {
    text-align: center;
  }
  .specialties__link {
    text-align: center;
    width: 100%;
  }
  .specialties__link span {
    display: inline-block;
  }

  .back-title-specialties {
    display: none;
  }

  .specialties-filter {
    display: none;
  }

  .specialties-payment-block {
    height: auto;
  }
  .specialties-payment-block__descr {
    display: none;
  }

  .specialties-payment-block-right__list {
    flex-direction: row;
    justify-content: space-between;
  }
  .specialties-payment-block-right__item {
    flex-direction: column;
  }
  .specialties-payment-block-right__item:first-child {
    display: none;
  }
  .specialties-payment-block-right__item:not(:last-child) {
    border-bottom: none;
  }

  .specialties-swiper {
    margin-bottom: 24px;
    overflow: hidden;
  }

  .back-title-about {
    display: none;
  }
}
@media (max-width: 991px) {
  .specialties__title {
    font: 500 23px/24px "Formular";
  }

  .specialties-pagination {
    text-align: center;
  }
}
.about {
  position: relative;
}
.about__title {
  margin-bottom: 16px;
  font: 500 31px/36px "Formular";
}
.about__pictures {
  display: grid;
  margin-bottom: 8px;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(2, 176px);
  gap: 8px;
}
.about__description {
  font: 400 16px/30px "Open Sans";
}

.back-title-about {
  position: absolute;
  top: 0;
  right: 0;
  font: 400 13px/20px "Open Sans";
  color: #767676;
}

.about-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-img--1 {
  border-radius: 10px 0 0 10px;
}
.about-img--3 {
  border-top-right-radius: 10px;
}
.about-img--5 {
  border-bottom-right-radius: 10px;
}

.about-pic {
  position: relative;
  display: block;
}
.about-pic--1 {
  grid-column: 1/7;
  grid-row: 1/3;
}
.about-pic--2 {
  grid-column: 7/9;
  grid-row: 1;
}
.about-pic--3 {
  grid-column: 9/11;
  grid-row: 1;
}
.about-pic--4 {
  grid-column: 7/9;
  grid-row: 2;
}
.about-pic--5 {
  grid-column: 9/11;
  grid-row: 2;
}

.btn-about-showmore {
  padding: 0;
  font: 500 18px/24px "Formular";
  color: #0656B4;
}

@media (max-width: 500px) {
  .about__description {
    padding: 24px;
  }
}