.container-content {
  position: relative;
}

.specialties {
  margin-bottom: $margin-section;

  &__title {
    font: $font-formular-title;
  }

  &__link {
    display: inline-block;
    margin-bottom: 22px;
    font: $font-opensans-link;
    color: $color-dscr;

    span {
      display: none;
    }
  }
}

.back-title-specialties {
  position: absolute;
  top: 0;
  right: 0;
  font: $font-opensans-link;
  color: $color-dscr;
}

.specialties-filter {
  display: flex;
  align-items: center;
  margin-bottom: 22px;

  &__title {
    margin-right: 24px;
    font: $font-opensans-link;
  }

  &__list {
    display: flex;
  }

  &__item {
    line-height: 20px;
  }

  &__item:not(:last-child) {
    margin-right: 24px;
  }

  &__btn {
    position: relative;
    padding: 0;
    padding-right: 24px;
    font: $font-opensans-link;
    color: $color-dscr;
  }
}

.filter-active {
  color:#222;
}

.filter-active::after {
  content: '';
  position: absolute;
  top: 7px;
  right: 0;
  width: 15px;
  height: 7px;
  background: url(../img/filter.svg) no-repeat center center;
}

.specialties-img {
  width: 100%;
  min-width: 168px;
  height: 100%;
  border-radius: 6px;
  object-fit: cover;
}

.specialties-payment-block {
  display: flex;
  margin-bottom: $margin-16;
  padding: 16px;

  &__left {
    display: flex;
  }
  
  &__right {
    display: flex;
  }

  &__pic {
    margin-right: $margin-16;
  }

  &__content {
    padding-right: $margin-little;
    max-width: 418px;
    border-right: 1px solid $color-border;
  }

  &__qualification {
    font-family: $OpenSans;
    font-size: 10px;
    font-weight: 600;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: $color-dscr;
  }
  &__title {
    margin-bottom: $margin-little;
    font: $font-formular-liltitle;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    margin-bottom: $margin-little;
  }

  &__item:not(:last-child) {
    margin-right: 6px;
  }

  &__link {
    display: inline-block;
    padding: 5px 12px;
    font-family: $OpenSans;
    font-size: 14px;
    line-height: 22px;
    white-space: nowrap;
    border: 1px solid $color-border;
    border-radius: 47px;
    transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out, color 0.2s ease-in-out;

    &:hover {
      border: 1px solid $color-action;
      background-color: $color-action;
      color: #fff;
      transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    }
  }

  &__descr {
    margin-bottom: 0;
    font: $font-opensans-link;
    line-height: 25px;
    color: $color-dscr;
  }

  &__openall {
    font-family: $OpenSans;
    font-size: 13px;
    font-weight: 600;
    line-height: 25px;
  }
}

.specialties-payment-block-right {
  padding-left: $margin-16;
  width: 100%;
  max-width: 270px;

  &__list {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__item {
    display: flex;
    padding: 12px 0;
  }

  &__item:first-child {
    padding-top: 0;
  }

  &__item:not(:last-child) {
    border-bottom: 1px solid $color-border;
  }

  &__item {
    display: flex;
    justify-content: space-between;
  }

  &__about {
    font:$font-opensans-liltitle;
    color: $color-dscr;
  }

  &__count {
    font-family: $Formular;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
  }
}

.specialties-payment-block-right-icon {
  display: flex;

  &__item:not(:last-child) {
    margin-right: 9px;
  }
}

.btn-show-all-specialties {
  padding: 15px 22px;
  font-family: $Formular;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: rgba(6, 86, 180, 0.13);
  color: $color-action;
}

.specialties-payment-block.is-hidden {
  display: none;
}

@media (max-width:1200px) {
  
  .specialties-filter {
    flex-direction: column;

    &__title {
      align-self: flex-start;
    }

    &__list {
      width: 100%;
    }

    &__btn {
      text-align: left;
    }
  }

  .specialties-payment {
    
    &__block {
      flex-direction: column;
    }
  }
  .specialties-payment-block {
  
    &__left {
      flex-direction: column;
    }

    &__pic {
      margin-right: 0;
      margin-bottom: $margin-16;
    }

    &__content {
      padding-right: 0;
      max-width: 100%;
      border: none;
    }

    &__qualification {
      display: block;
      margin-bottom: 10px;
    }

    &__descr {
      margin-bottom: 10px;
    }

    &__openall {
      display: none;
    }

    &__right {
      padding-left: 0;
      max-width: 100%;
    }
  }
}

@media (max-width:1200px) {

  .specialties {
    &__title {
      text-align: center;
    }

    &__link {
      text-align: center;
      width: 100%;

      span {
        display: inline-block;
      }
    }
  }

  .back-title-specialties {
    display: none;

  }

  .specialties-filter {
    display: none;
  }

  .specialties-payment-block {
    height: auto;

    &__descr {
      display: none;
    }
  }

  .specialties-payment-block-right {

    &__list {
      flex-direction: row;
      justify-content: space-between;
    }

    &__item {
      flex-direction: column;
    }

    &__item:first-child {
      display: none;
    }

    &__item:not(:last-child) {
      border-bottom: none;
    }
  }

  .specialties-swiper {
    margin-bottom: 24px;
    overflow: hidden;
  }

  .back-title-about {
    display: none;
  }
}

@media (max-width:991px) {

  .specialties {
    &__title {
      font: $font-formular-mobile-title;
    }
  }

  .specialties-pagination {
    text-align: center;
  }
}
