.header-page {
  margin-bottom: 30px;
  padding-bottom: 0;
  background-color: transparent;
}

.header-info-row {
  margin: 0;
}

.breadcrumb-container {
  margin-bottom: 31px;
  padding: 0;
}

.breadcrumb-item {
  display: inline-flex;
  align-items: center;
}

.breadcrumb-item + .breadcrumb-item {
  padding: 0;
}

.breadcrumb-item + .breadcrumb-item:before {
  padding: 0 1.12rem;
  content: '';
  width: 6px;
  height: 12px;
  background: url('../img/arrow-right.svg') no-repeat center center;
}

.header-info-pic {
  
  img {
    width: 100%;
    height: 100%;
    border-radius: 10px 10px 0 0;
  }
}

.header-info {

  padding-bottom: 22px;
  border-radius: 10px;
  background-color: #fff;

  &__logo {
    position: absolute;
    top: 24px;
    left: 24px;
    display: block;
    border-radius: 10px;
    background-color: #fff;
  }

  &__top {
    position: relative;
    padding: 0;
  }

  &__bottom {
    margin-bottom: $margin-16;
    padding: 25px 29px 0 24px;
  }

  &__descr {
    padding-right: 25px;
    text-align: end;
    font-size: 12px;

    span {
      margin-right: $margin-16;
      color: $color-dscr;
    }
    
    a {
      font-weight: 700;
      color: $color-action;
      transition: color 0.2s ease-in-out;

      &:hover {
        color: #222;
        transition: color 0.2s ease-in-out;
      }
    }
  }
}

.header-info-bottom {

  &__left {
    padding-top: 4px;
  }

  &__title {
    margin-bottom: 5px;
    max-width: 77%;
    text-align: left;
    font: $font-formular-title;
  }

  &__about {
    display: flex;
    align-items: center;
  }
}

.header-info-bottom-about {
  &__rating {
    display: inline-flex !important;
    align-items: center;

    svg {
      margin-right: 5.5px;
      fill: #EFD967;
    }

    strong {
      margin-right: 4px;
    }

    a {
      font-family: $Formular;
      font-size: 15px;
      line-height: 20px;
      color: $color-dscr;
      transition: color 0.2s ease-in-out;

      &:hover {
        color: #222;
        transition: color 0.2s ease-in-out;
      }
    }
  }

  &__point {
    display: inline-block;
    margin: 0 1.1rem;
    min-width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $color-dscr;
  }

  &__dscr {
    font: $font-opensans-descr;
    color: $color-dscr;

    a {
      color: $color-dscr;
      transition: color 0.2s ease-in-out;

      &:hover {
        color: #222;
        transition: color 0.2s ease-in-out;
      }
    }
  }
}

.info-logo {
  padding: 4px 6.5px;
}

.btn-feedback {
  position: absolute;
  right: 19rem;
  bottom: 24px;
  padding: 16px 23px;
  font-family: $Formular;
  font-weight: 500;
  font-size: 10px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: $color-action;
  color: #fff;
  transition: background-color 0.2s ease-in-out;

  &:hover {
    background-color: #0657b4c2;
    transition: background-color 0.2s ease-in-out;
    color: #fff;
  }
}

.btn-check-chance {
  position: absolute;
  right: 1rem;
  bottom: 24px;
  padding: 16px 23px;
  font-family: $Formular;
  font-weight: 500;
  font-size: 10px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: #DFE9F5;
  color: $color-action;
  transition: color 0.2s ease-in-out;

  &:hover {
    color: #222;
    transition: color 0.2s ease-in-out;
  }

}

.share-buttons {
  position: absolute;
  right: 30px;
  bottom: -64px;
  justify-content: flex-end;
  align-items: center;
  margin-left: 0;
  padding-top: 3px;
}

.btn-favorite {
  display: inline-flex;
  align-items: center;
  margin-right: 8px;
  padding: 0 15px;
  fill: $color-icons;
  transition: background-color 0.2s ease-in-out;

  &:hover {
    transition: background-color 0.2s ease-in-out;
  }

  span {
    padding-left: 8px;
  }
}

.btn-bell {
  margin-right: 13px;
  padding: 0;
  max-width: 32px;
  max-height: 32px;
  fill: transparent;
  border-radius: 50%;
  stroke: $color-icons;
  transition: background-color 0.2s ease-in-out;

  &:hover {
    background-color: #eee;
    transition: background-color 0.2s ease-in-out;
  }
}

.btn-share {
  padding: 0;
  max-width: 32px;
  max-height: 32px;
  border: transparent;
  fill: $color-icons;
  transition: background-color 0.2s ease-in-out;

  &:hover {
    transition: background-color 0.2s ease-in-out;
  }
}


@media (max-width:1200px) {

  .header-info-bottom-about {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &__rating {
      order: 1;
    }

    &__point {
      display: none;
    }
  }

  .btn-favorite {
    padding: 11px;
    border-radius: 50%;

    span {
      display: none;
    }
  }

  
}

@media (max-width:991px) {

  .header-page {
    margin-bottom: 76px;
  }
  
  .back-breadcrumb {
    margin-bottom: 12px;
    padding-left: 37px;
    font-family: $Formular;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    line-height: 16px;
    letter-spacing: 2px;
  }

  .back-breadcrumb::before {
    content: '';
    margin-right: 13px;
    width: 6px;
    height: 12px;
    background: url(../img/arrow-right.svg) no-repeat center center; 
    transform: rotate(180deg);
  }

  .btn-feedback {
    display: none;
  }

  .header-info {
    position: relative;
    margin-top: 0;
    padding-bottom: 80px;

    &__top {
      position: unset;
      padding: 307px 8px 8px 0;
      border-radius: 10px 10px 0 0;
      background: url(../img/back-bauman.jpg) no-repeat top center;
      background-size: cover;
    }

    &__logo {
      top: 8px;
      left: 8px;
    }

    &__bottom {
      margin-bottom: 0;
    }

    &__descr {
      position: absolute;
      right: 16px;
      bottom: -48px;
      left: 16px;
      padding-right: 0;
    }
  }

  .header-info-pic {
    display: none;
  }

  .share-buttons {
    position: relative;
    right: unset;
    bottom: unset;
    margin-top: 0;
  }

  .btn-check-chance {
    right: unset;
    bottom: 16px;
    left: 16px;
  }

  .btn-favorite {
    margin-right: 0;
    background-color: #fff;
    order: 1;
  }

  .btn-bell {
    display: none;
  }

  .btn-share {
    display: inline-flex;
    align-items: center;
    margin-right: $margin-little;
    padding: 11px;
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
    background-color: #fff;
  }
}

@media (max-width:500px) {
  
  .header-info {

    &__top {
      padding-top: 71px;
      border-radius: unset;
      background: url(../img/back-bauman_mobile.jpg) no-repeat top center;
      background-size: cover;
    }

    &__bottom {
      padding: 16px 16px 0 16px;
    }

    &__descr {
      display: flex;
      justify-content: space-between;
    }
  }

  .header-info-bottom {

    &__title {
      max-width: 100%;
      font: $font-formular-mobile-liltitle;
    }
  }

  .header-info-bottom-about {

    &__dscr {
      font: $font-opensans-link;
    }

    &__rating {
      font-size: 15px;
      line-height: 20px;
    }
  }

  .btn-check-chance {
    right: 16px;
  }
}